import React, { Fragment } from "react";
import { useState, useEffect } from "react";
import Header from "../Header";
import { onSnapshot, doc, deleteDoc, updateDoc } from "firebase/firestore";
import { app } from "../../../login/firebase";
import { getFirestore, getDocs, getDoc, collection, snapshotEqual } from "firebase/firestore";
import "./AllButtons.css";
import { makeStyles,Grid, Paper } from "@material-ui/core";
import KebabMenu from "./kebab-menu";
import useSpeechSynthesis from "react-speech-kit/dist/useSpeechSynthesis";


const useStyles = makeStyles((theme) =>({
    grid: {
        width: '100%',
        margin: '0px'
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        background: theme.palette.success.light,
        backgroundSize: 'contain'
    }
}));
const AllButtons = () =>{
    const { speak } = useSpeechSynthesis();

    const headervalue = 'All Buttons';
    const db = getFirestore(app);
    const [buttons, setButtons] = useState([]);
    const [color, setColor] = useState("");
    
    const listButtons = async () => {

        onSnapshot(collection(db, "buttons"), (snapshot) => {
            setButtons(snapshot.docs.map(doc => doc.data()));
            console.log(snapshot.docs.map(doc => doc.data()))
        });
    }

    useEffect(() => {
        listButtons();
    }, []);
    const classes = useStyles()
    return(
        <Fragment>
        <div className="AllButtons">
            <Header text={headervalue}/>
            <div className="AllButtons-area">
                <h3>All Buttons</h3><br/>
                <Grid container spacing={1} className={classes.grid}>
                    {buttons.map((val, id) => {
                        return (<Grid item xs={6} md={3}>
                                    <Paper className={classes.paper} >
                                    <KebabMenu val={val}/>
                                    <img id="preview-image" src={val.image_url} onClick={() => speak({ text: val.name })}/>
                                    </Paper>
                                </Grid>
                        );
                    })}
                    </Grid>
            </div>
        </div>
        </Fragment>
    );
}
export default AllButtons;